import Vue from "vue";
import App from "./PopupApp";

global.browser = require("webextension-polyfill");
Vue.prototype.$browser = global.browser;

let isFirefox =
  typeof navigator !== "undefined" &&
  navigator.userAgent &&
  navigator.userAgent.toLowerCase().indexOf("firefox") > -1;

if (isFirefox) {
  document.getElementById("app").style.display = "none";
  document.getElementById("opt-in-prompt").style.display = "block";

  function showApp() {
    document.getElementById("opt-in-prompt").style.display = "none";
    document.getElementById("app").style.display = "block";

    new Vue({
      el: "#app",
      render: (h) => h(App),
    });
  }

  window.addEventListener("load", function () {
    browser.storage.local.get("optInShown").then((result) => {
      if (result.optInShown) {
        showApp();
      }
    });

    document
      .getElementById("button-enable")
      .addEventListener("click", function () {
        browser.storage.local.set({ optIn: true, optInShown: true });
        showApp();
      });

    document
      .getElementById("button-cancel")
      .addEventListener("click", function () {
        browser.storage.local.set({ optIn: false, optInShown: true });
        showApp();
      });
  });
} else {
  /* eslint-disable no-new */
  new Vue({
    el: "#app",
    render: (h) => h(App),
  });
}
