<template>
  <div class="App">
    <iframe
      v-if="loadIframe"
      v-show="iframeLoaded"
      @load="iframeLoad"
      style="overflow: hidden; width: 100vw; height: 100vh; display: flex"
      ref="dedalium-iframe"
      class="App_iframe"
      :src="getExtensionUrl()"
      frameBorder="0"
      seamless
    ></iframe>
    <CustomLoader v-if="!iframeLoaded"></CustomLoader>
  </div>
</template>

<script>
import CustomLoader from './Loader.vue'
import Cookies from '../services/Cookies'

export default {
  components: {
    CustomLoader,
  },
  data() {
    return {
      loadIframe: false,
      iframeLoaded: false,
      locale: '',
    }
  },
  mounted() {
    browser.runtime.sendMessage({ popupOpen: true })

    Cookies.get(process.env.URL, 'i18n_redirected').then((locale) => {
      if (locale) {
        this.locale = locale.value
      }
    })

    browser.runtime.onMessage.addListener((request) => {
      if (request.action === 'dedaliumUpdated') {
        this.$refs['dedalium-iframe'].contentWindow.postMessage('updateCounter', '*')
      }
    })

    setTimeout(() => {
      this.loadIframe = true
    }, 100)
  },
  methods: {
    iframeLoad() {
      setTimeout(() => {
        this.iframeLoaded = true
      }, 100)
    },
    getExtensionUrl() {
      return (
        process.env.URL +
        (this.locale && this.locale !== 'en' ? '/' + this.locale : '') +
        '/extension/'
      )
    },
  },
}
</script>
