let browser = require("webextension-polyfill");

// Only use this service for background script!
// https://developer.chrome.com/extensions/cookies

const getAll = (url, details = {}) => {
  return browser.cookies.getAll(Object.assign({ url }, details));
};

const get = (url, name) => {
  return browser.cookies.get(Object.assign({ url, name }));
};

const remove = (url, name) => {
  return new Promise((resolve, reject) => {
    browser.cookies.remove({ url, name }).then(() => {
      if (browser.runtime.lastError) {
        console.log(
          `error deleting cookie ${name}: ` + browser.runtime.lastError.message
        );
        reject(
          new Error(
            `error deleting cookie ${name}: ` +
              browser.runtime.lastError.message
          )
        );
      } else {
        // console.log(`Removed cookie ${name}`);
        resolve(name);
      }
    });
  });
};

const set = (url, name, value, details = {}) => {
  return new Promise((resolve, reject) => {
    details = Object.assign(
      {
        url,
        name,
        value,
        domain: null,
        path: "/",
        secure: true,
        httpOnly: false,
        sameSite: "no_restriction",
        expirationDate: 2054553823, // year 2035
      },
      details
    );

    browser.cookies.set(details).then(() => {
      if (browser.runtime.lastError) {
        console.log(
          `error setting cookie ${name}: ` + browser.runtime.lastError.message
        );
        reject(
          new Error(
            `error setting cookie ${name}: ` + browser.runtime.lastError.message
          )
        );
      } else {
        resolve(name);
      }
    });
  });
};

export default {
  getAll,
  get,
  remove,
  set,
};
